<script>
import Header from '../../../../components/school-training/Header.vue'
import HorizontalTap from '../../../../components/tap/HorizontalTap.vue'
import getDateStr from '@/utils/getDateStr'
export default {
  name: 'app',
  components: {
    Header,
    HorizontalTap
  },
  mounted () {
    this.initEvent()
    this.initData()
  },
  data () {
    return {
      winWidth: window.innerWidth,
      selectedDay: '',
      data2: [{
        name: '执行任务',
        url: 'task-list.html',
        type: '_self'
      }, {
        name: '任务单',
        url: 'work-order.html',
        type: '_self'
      }, {
        name: '日报任务',
        url: '',
        type: '_self'
      }, {
        name: '相关资料',
        url: 'learning-library.html',
        type: '_self'
      }, {
        name: '成长轨迹',
        url: 'growth-locus.html',
        type: '_self'
      }],
      options: [{
        value: '1',
        label: '1小时'
      }, {
        value: '2',
        label: '2小时'
      }, {
        value: '3',
        label: '3小时'
      }, {
        value: '4',
        label: '4小时'
      }, {
        value: '5',
        label: '5小时'
      }],
      value: '',
      todayDaily: '',
      tomorrowDaily: '',
      calendarValue: '',
      showAllDaily: 0,
      dailyList: [],
      reportDaysList: [], // 该月哪天提交了日报
      calendarflg: false
    }
  },
  methods: {
    initEvent: function () {
      let me = this
      me.winWidth = $('body').width()
      window.onresize = function () {
        me.winWidth = $('body').width()
      }
    },
    initData: function () {
      var today = getDateStr(0) // 获取当前日期
      this.selectedDay = today
      this.loadReportDays(today)
      this.loadReport(today)
    },
    sizeChange (size) {
      return this.winWidth * size / 1920 + 'px'
    },
    submitDailyReport () {
      let me = this
      if (me.todayDaily === '' || me.todayDaily === null || me.todayDaily === undefined) {
        me.$message.error('日报内容不能为空！')
        return false
      }
      me.$http.post('/doproject/dailyreport/sumbit', { 'teamid': localStorage.getItem('teamId'), 'content': me.todayDaily }, res => {
        if (res.success) {
          me.loadReportDays(getDateStr(0))
          me.loadReport(getDateStr(0))
        }
      })
    },
    changeMonth (month) {
      this.reportDaysList = []
      this.calendarflg = false
      let dateStr = month + '-01'
      this.loadReportDays(dateStr)
    },
    dayInput (day) {
      if (day > getDateStr(0)) {
        this.$message.error('不能查看今天之后的内容！')
        return false
      }
      this.loadReport(day)
    },
    loadReportDays (dateStr) {
      let me = this
      me.$http.post('/doproject/dailyreport/reporteddays', { 'teamid': localStorage.getItem('teamId'), 'dateStr': dateStr }, res => {
        // console.log(res.data)
        if (res.success && res.data.length > 0) {
          for (var i = 0; i < res.data.length; i++) {
            me.reportDaysList.push(parseInt(res.data[i].substr(8, 2)))
          }
        } else {
        }
        me.calendarflg = true
      })
    },
    loadReport (dateStr) {
      let me = this
      me.$http.post('/doproject/dailyreport/detail', { 'teamid': localStorage.getItem('teamId'), 'date': dateStr }, res => {
        if (res.success) {
          if (res.data.length > 0) {
            me.dailyList = res.data
            me.selectedDay = dateStr
            me.showAllDaily = 1
          } else if (dateStr !== getDateStr(0)) {
            me.showAllDaily = 2
            me.selectedDay = dateStr
          } else {
            me.showAllDaily = 0
          }
        }
      })
    }
  }
}
</script>

